.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 10px;
    background-color: white;
    z-index: 1;

    .title {
      font-weight: bold;
      margin: 10px 0;
      font-size: 24px;
    }

    .product__image {
        height: 150px;
        object-fit: contain;
        margin: 75px 0 0 15px;

        &:hover {
          scale: 1.3;
          transition: all 1s;
          -webkit-transition: all 1s;
        }
      }
      
    .product__price {
        margin: 10px 0;
        font-weight: bold;
      }
      
    .product__info {
        height: 100px;
        margin-bottom: 15px;
      }

    .product__rating {
        display: flex;
        color: orange;
    }

  button {
    width: 200px;
    background: transparent;
    border: 1px solid;
    margin-top: 20px;
    color: orange;
    padding: 6px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    border: orange 1px solid;

    &:hover {
      background-color: orange;
      color: white;
    }
  }
}