.header {
    background-color: #131921;
}

.header-nav {
    height: 60px; 
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 100;
    max-width: 1440px;
    padding: 0 20px;
    margin: auto;
}

.header__logo {
    display: flex;
    align-items: center;
    color: #ff9f00;
    margin: 0 25px 0 0;
    cursor: pointer;
}

.header__logoImage {
    margin-right: 10px;
}

.header__logoTitle {
    display: none;
    text-decoration: none;
    border: 0;
    color: white;
}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
    margin: 0 40px 0 auto;
    max-width: 500px;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: #ff9f00;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.nav__item {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;  
    color: white;
    cursor: pointer;
}

.nav__itemLineOne {
    font-size: 10px;
}

.nav__itemLineTwo {
    font-size: 13px;
    font-weight: 800; 
    position: relative;

    .nav__basketCount {
        position: absolute;
        right: -15px;
        top: -15px;
        background-color: white;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        color: black;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
        font-weight: 700;
    }
}

.nav__itemBasket {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    margin-right: 10px;
    margin-left: 10px;
}

@media screen and (min-width: 760px) {
    .header__logoTitle {
        display: block;
    }
}